	export default {
		data() {
			return {
				disabled:true,
				id1:"",
				password1:"",
		
			}
		},
		
		methods: {
			getNum(e){
				this.id1 = e.detail.value
				if(this.id1 !=null && this.id1 !="" && this.id1 != undefined && this.password1 !=null && this.password1 !="" && this.password1 != undefined){
					this.disabled = false;
				}else {
					this.disabled = true;
				}
			},
			getPass(re){
				this.password1 = re.detail.value
				if(this.id1 !=null && this.id1 !="" && this.id1 != undefined && this.password1 !=null && this.password1 !="" && this.password1 != undefined){
					this.disabled = false;
				}else {
					this.disabled = true;
				}
			},
			formSubmit(e){
				var me = this;
				/* 获取登录id和password */
				var id = e.detail.value.id;
				var password = e.detail.value.password;
				//发起登录请求
				uni.request({
					url: "http://134.175.204.38:66/user/login",
					data:{
						"userId":id	,			//引号内是数据库字段
 						"userPassword":password
					},
					method: 'POST',
					success: res => {
						//获取真实数据之前务必判断状态是否200.
						if(res.data.result === "succes"){
						var userInfo = res.data.message;
						uni.setStorageSync("globalUser",userInfo); //将用户信息保存
						uni.switchTab({
							url:"../me/me" //页面跳转
						})
						}else {
							uni.showToast({
								title:"输入的信息有误，请检查",
								duration:2000,
								image:"../../static/icon/error.png"
							})
						}
							},
					fail: () => {},
					complete: () => {
					},
				});
			}
		}
	}
